import { configabi } from '../../../abi/configabi';
import config from '../../../DApp/config'
let {LPInit} = config;
export default{
   
    data(){
    
        return{
            idx:-1,
            expands: [],
            getRowKeys(row){
              return row.pid
            },
            duration:2000,
            totalTvl:0,
            ClaimBtns:[],
            tableDataBackups:[],
            currLp:{},
            currLpWeb3:{},

            OecPool:{},
            type:-1,
            num:"",
            flag : false,
            dialogVisible:false,
            value: false,
            input:"",
            btns:{
              claim:false,
              add:false,
              withdraw:false,
              confrim:true,
              approve:false
            },
            tableData: [
              
            ],
              types:[
                {
                  isLink:false,
                  tit:"取回USDT/COCO LP",
                  request:false,
                  balance:0
                },
                {
                  isLink:true,
                  tit:"Deposit pool Tokens",
                  request:false,
                  balance:0
                },
              ],
              dialog:{
                isLink:false,
                tit:"取回USDT/COCO LP",
                request:false,
                balance:0,
                isApprove:false

              },
              form:{
                _pid:-1,
                _amount:""
              }
              
        
        
        }
        
        },
    computed:{
    
    
    
    },
    watch:{
      'form._amount'(curr,old){
        curr = Number(curr);
        //console.log(curr);

        if(curr===""||curr<=0)
        this.btns.confrim = true;
        else
        this.btns.confrim = false;

        
      }
    },
    mounted() {
      
      

    },
    methods:{
      open4() {
        
        this.$notify({
          title: 'Error',
          message: 'Transaction Declined',
          type: 'warning'
        });
      },
      open2() {
        this.$notify({
          title: 'Success',
          message: 'Transaction Submitted',
          type: 'success',
          showClose:true
        });
      },
      expandChange(row,expandedRows) {
        let that=this
        // console.log(expandedRows);
        // console.log(row);
        //只展开一行
        if (expandedRows.length) {//说明展开了
          that.expands = []
          if (row) {
            that.expands.push(row.pid)//只展开当前行id
          }
        } else {//说明收起了
          that.expands = []
        }
      },
    async pending(_pid,user) {
      
      try {
          
          const res = await this.OecPool.pending(_pid,user);
          //console.log(res);
          return res;
      } catch (e) {
          console.log(e);
      }
  
    },
    async userInfo(_pid,user) {
        
        try {
            
            const res = await this.OecPool.userInfo(_pid,user);
            //console.log(res);
            return res;
        } catch (e) {
            console.log(e);
        }
    
    },
    async deposit({_pid,_amount}) {
        
        try {
            _amount = this.OecPool.web3.utils.toWei(_amount.toString());
            //console.log(_amount)
            const res = await this.OecPool.deposit(_pid,_amount);
            //console.log(res);
            if(res===undefined)
            this.dialog.request = false;
            else{
              const {status} = res;
              this.dialog.request = !status;
              this.dialogVisible = false;
              this.refreshData();


            }
        } catch (e) {
            console.log(e);
            this.dialog.request = false;
        }
    
    },
    async approve({addr,_amount}) {
        
      try {
          _amount = 11579.20892373162;
          
          _amount = this.OecPool.web3.utils.toWei(_amount.toString());
          _amount = this.OecPool.web3.utils.toWei(_amount.toString());
          _amount = this.OecPool.web3.utils.toWei(_amount.toString());
          _amount = this.OecPool.web3.utils.toWei(_amount.toString());



          // console.log(_amount)
          const gasAmount = await this.currLpWeb3.meta.methods.approve(addr,_amount).estimateGas({ from: this.currLpWeb3.account });
          const res = await this.currLpWeb3.meta.methods.approve(addr,_amount).send({from:this.currLpWeb3.account,gas: gasAmount});
          // console.log(res);
          if(res===undefined)
          this.dialog.request = false;
          else{
            const {status} = res;
            this.dialog.request = false;
            this.dialog.isApprove = true;
            


          }
      } catch (e) {
          console.log(e);
          this.dialog.request = false;
      }
  
  },
    async withdraw({_pid,_amount}) {
        
        try {
            _amount = this.OecPool.web3.utils.toWei(_amount.toString());
            //console.log(_amount);
            
            const res = await this.OecPool.withdraw(_pid,_amount);

            //console.log(res);
            if(res===undefined)
            this.dialog.request = false;
            else{
              const {status} = res;
              this.dialog.request = false;
              this.dialogVisible = false;
              this.refreshData();


            }
        } catch (e) {
            console.log(e);
            this.dialog.request = false;

        }
    
    },
    tableHandle(){
      let {tableData} = this;
      tableData.forEach(item => {
        item.ClaimStatus = false;
        item.flag = false;

      });
      this.tableData = tableData;
      //console.log(tableData)
    },
    
    nav(){
    
    this.$router.push('')
    
    },
    clickTable(row,index,e){
                    this.$refs.refTable.toggleRowExpansion(row);
                    row.flag = !row.flag;
                    // //console.log(row)


               },
    handle(num,row,idx){
      let {pid,lpToken,myAmount} = row;
      this.form._pid = pid;
      // console.log(idx);
      this.idx = idx;
      this.dialog.balance = 0;
      this.dialogVisible = true;
      this.dialog = this.types[num];
      this.type = num;
      this.currLp = row;
      if(num==1)
      this.getPLBalance(lpToken);
      else
      this.dialog.balance = myAmount;

      this.formInit();
      
      
    },
    claim(idx,row){
      // console.log(idx,row);
      this.claimHandle({_pid:row.pid,_amount:0,idx});

      this.tableData[idx].ClaimStatus = true;
      this.refreshTableData();
      
      
    },
    async claimHandle({_pid,_amount,idx}) {
        
      try {
          // _amount = this.OecPool.web3.utils.toWei(_amount.toString());
          //console.log(_amount)
          const res = await this.OecPool.deposit(_pid,_amount);
          console.log(res);
          this.tableData[idx].ClaimStatus = false;
            this.refreshTableData();
          if(res===undefined)
            this.open4();
          
          else
            
            this.open2();
          
          

      } catch (e) {
          console.log(e);
          this.tableData[idx].ClaimStatus = false;

          this.refreshTableData();
      }
  
  },
    refreshTableData(){
      
      this.$emit('ClaimStatusHandle',this.tableData);
      
      
    },
    fromWei(num){
      num = this.OecPool.web3.utils.fromWei(num.toString());
      return num;
    },
    toWei(num){
      num = this.OecPool.web3.utils.toWei(num.toString());
      return num;
    },
    confrim(){
      this.dialog.request = true;
      let {form,type,toWei} = this;

      form._amount = form._amount.trim();
      //console.log(form,type);
      if(type==0)
      this.withdraw(form);

      else
      this.deposit(form);



    },
    appr(){
      this.dialog.request = true;
      let {form,type,toWei,currLp} = this;

      form._amount = form._amount.trim();
      // console.log(currLp);
      let obj = {addr:configabi.OecPoolAddress,_amount:form._amount};
      this.approve(obj);
      



    },
    async PLInit(addr) {
      try {
          const res = await LPInit(addr);
          // console.log(res);
          return res;
      } catch (e) {
          console.log(e);

      }
      
    },
    async getPLBalance(addr){
      //console.log(addr);
      try {
        const res = await this.PLInit(addr);
        // console.log(res);
        this.currLpWeb3 = res;
        const {meta,account,web3} = res;
        const {balanceOf,allowance} = meta.methods;
        let balance = await balanceOf(account).call();
        balance = web3.utils.fromWei(balance.toString());
        //console.log(balance);
        balance = Number(balance).toFixed(5);
        this.dialog.balance = balance;

        let apprAmount = await allowance(account,configabi.OecPoolAddress).call();
        apprAmount = web3.utils.fromWei(apprAmount.toString());
        // console.log(Number(apprAmount)>0);
        
         this.dialog.isApprove = Number(apprAmount)>0;
        

        

      } catch (e) {
          console.log(e);

      }
    },
    max(){
      this.form._amount = this.dialog.balance;
    },
    formInit(){
      this.form._amount = "";
    },
    change(e){
      // console.log(e);
      if(e)
        this.tableData = this.tableData.filter(item=>{
          return item.myAmount>0;
        })
      
      else
        this.tableData = this.tableDataBackups;

      
    },
    search(e){
      // console.log(e);
      e = e.trim();
      if(e.length>0)
        this.tableData = this.tableData.filter(item=>{
          return item.name.toLowerCase().includes(e);
        })
      else
      this.tableData = this.tableDataBackups;

      

    },
    refreshData(){
      this.$emit('refresh');
    },

    
    },
    
    
    
    
    
    }