import {getPoolLists} from '@/api/mining';
import { Number } from 'core-js';
export default{
    data(){
    
        return{
            datatimer:null,
            ClaimBtns:[],
            OecPool:{},
            account:"",
            coins:{
                USDT:require("../../assets/img/mining/USDT.png"),
                BTCK:require("../../assets/img/mining/BTCK.png"),
                DAIK:require("../../assets/img/mining/DAIK.png"),
                ETHK:require("../../assets/img/mining/ETHK.png"),
                WOKT:require("../../assets/img/mining/WOKT.png"),
                any:require("../../assets/img/mining/any.svg"),
                pgo:require("../../assets/img/wallet/pgo.png"),
                






            }
        
        
        }
        
        },
    computed:{
    
    
    
    },
    beforeDestroy() {
        clearInterval(this.datatimer);
        this.datatimer = null;
      },
    destroyed() {
        clearInterval(this.datatimer);
        this.datatimer = null;
    },
    mounted() {
        let {OecPoolInit,setWeb3,flowInit,init,clock} = this;
        OecPoolInit(obj=>{
            //console.log(account);
            init();
            this.OecPool = obj;
            setWeb3(obj);
            let {account} = obj;
            this.account = account;
            //console.log(account);
            
            
        });
        // flowInit(obj=>{
        //     this.OecPool = obj;
        //     setWeb3(obj);
        //     let {account} = obj;
        //     this.account = account;
        //     //console.log(account);
            
            
        // });


        
        setTimeout(() => {
            clock();
            
        }, 10000);
        
  
      },
    methods:{
        clock(){
            this.datatimer = setInterval(() => {
                this.refresh();
            }, 10000);
        },
        async pending(_pid,user) {
            try {
                
                const res = await this.OecPool.pending(_pid,user);
                //console.log(res);
                return res;
            } catch (e) {
                console.log(e);
            }
        
        },
        async userInfo(_pid,user) {
        
            try {
                
                const res = await this.OecPool.userInfo(_pid,user);
                //console.log(res);
                return res;
            } catch (e) {
                console.log(e);
            }
        
        },
        switchIcon(key){
            let icon = "";
            let {coins} = this;
            switch (key) {
                
                    case "USDT":
                        icon = coins.USDT;
                        break;
                    case "BTCK":
                        icon = coins.BTCK;
                        break;
                    case "DAIK":
                        icon = coins.DAIK;
                        break;
                    case "ETHK":
                        icon = coins.ETHK;
                        break;
                    case "WOKT":
                        icon = coins.WOKT;
                        break;
                    case "PGO":
                        icon = coins.pgo;
                        break;
                    default:
                        icon = coins.any;

                        break;
            }
            return icon;
        },
        async getYield(item,account){
            try {
                let {pid,token0_symbol,token1_symbol} = item;
                let res = await this.pending(pid,account);
                let myAmount = await this.userInfo(pid,account);
                if(res===undefined)
                res = 0;
                else
                res = res[0];
                res = this.OecPool.web3.utils.fromWei(res.toString());
                if(res>0)
                res = Number(res).toFixed(6);
                item.yield = res;
                item.myAmount = this.OecPool.web3.utils.fromWei(myAmount.amount.toString());
                item.token0_icon = this.switchIcon(token0_symbol);
                item.token1_icon = this.switchIcon(token1_symbol);

                return item;
            } catch (err) {
              console.log(err);
            }
            
        },
        async initTableData(arr){
            let {account,getYield} = this;
            let arrs = [];
            let totalTvl = 0;
            arr.forEach((item,index) => {
                item.mulpitlier = Number(item.mulpitlier)>0?item.mulpitlier:0;
                item.mulpitlier = item.mulpitlier+'x';
                item.totalTvl = Number(item.totalTvl).toFixed(2);
                item.url = `https://www.oklink.com/okexchain/address/${item.lpToken}`;
                item.ClaimStatus = false;
                this.ClaimBtns[index] = {ClaimStatus : false};
                totalTvl+=Number(item.totalTvl);
                let apr = item.apr.split('%')[0];
                apr = apr.split(',');
                
                if(apr.length>1)
                    apr = Number(apr[0])*1000+Number(apr[1]);
                
                else
                    apr = apr[0];

                if(item.totalTvl==0)
                apr=0;

                item.apr = apr;
                


                let res = getYield(item,account);
                arrs.push(res);
            });
            //console.log(totalTvl);
            this.setTvl(totalTvl);
            try {
              let res = await Promise.all(arrs);
              //console.log(res);
              return res;
            } catch (err) {
              console.log(err);
            }
            
            
        },
        setStorage(key,obj){
            obj = JSON.stringify(obj);
            localStorage.setItem(key,obj);
        },
        getStorage(key){
            let res = localStorage.getItem(key);
            res = JSON.parse(res);
            return res;

        },
        
        async initTable(arr){
            let {account,getYield} = this;
            let arrs = [];
            let totalTvl = 0;
            arr.forEach((item,idx) => {
                item.mulpitlier = Number(item.mulpitlier)>0?item.mulpitlier:0;
                item.mulpitlier = item.mulpitlier+'x';
                item.totalTvl = Number(item.totalTvl).toFixed(2);
                item.url = `https://www.oklink.com/okexchain/address/${item.lpToken}`;
                totalTvl+=Number(item.totalTvl);
                // console.log(this.ClaimBtns);
                // console.log(this.ClaimBtns[idx].ClaimStatus);

                item.ClaimStatus = this.ClaimBtns[idx].ClaimStatus;
                let apr = item.apr.split('%')[0];
                apr = apr.split(',');
                if(apr.length>1)
                    apr = Number(apr[0])*1000+Number(apr[1]);
                
                else
                    apr = apr[0];

                if(item.totalTvl==0)
                apr=0;
                item.apr = apr;
                


                let res = getYield(item,account);
                arrs.push(res);
            });
            this.setTvl(totalTvl);
            try {
              let res = await Promise.all(arrs);
              return res;
            } catch (err) {
              console.log(err);
            }
            
            
        },
        async OecPoolInit(callback) {
            try {
                const res = await this.$DApp.OecPool.init();
                //console.log(res);
                callback&&callback(res);
            } catch (e) {
                console.log(e);
    
            }
            
          },
        async flowInit(callback) {
            try {
                const res = await this.$DApp.flow.init();
                //console.log(res);
                callback&&callback(res);
            } catch (e) {
                console.log(e);
    
            }
            
          },
        async getPoolLists(callback) {
            try {
                const res = await getPoolLists();
                const {farm} = res.data;
                //console.log(res);

                let array = [...farm];
                array = array.filter(item=> {return item.pid>0;} );


                callback&&callback(array);
                
            } catch (e) {
                console.log(e);
    
            }
            
          },
          setWeb3(obj){
              this.$refs.pc.OecPool = obj;
              this.$refs.mobile.OecPool = obj;

          },
          setTvl(num){
            this.$refs.pc.totalTvl = num;
            this.$refs.mobile.totalTvl = num;
          },
          setTableData(arr){
              this.tableData = arr;
              this.$refs.pc.tableData = arr;
              this.$refs.mobile.tableData = arr;
              this.$refs.pc.tableDataBackups = arr;
              this.$refs.mobile.tableDataBackups = arr;
              this.refreshTableData(arr);
          },
          refreshTableData(arrs){
            
            let arr = [];
            let newArr = [...arrs];
            newArr.forEach(item=>{
              let {ClaimStatus} = item;
              arr.push({ClaimStatus});
            });
            this.$refs.pc.ClaimBtns = arr;
            this.$refs.mobile.ClaimBtns = arr;
            
          },
          init(){
            let {getPoolLists,setTableData,initTableData} = this;

            getPoolLists((arr)=>{
                initTableData(arr).then(res=>{
                    // console.log(res);
                    setTableData(res);
    
                }).catch(err=>{
                    console.log(err);
                });
            });
          },
          refresh(){
            //   console.log(111);
            let {getPoolLists,setTableData,initTable} = this;

            getPoolLists((arr)=>{
                initTable(arr).then(res=>{
                    // console.log(res);
                    
    
                    setTableData(res);
    
                }).catch(err=>{
                    console.log(err);
                });
            });
          },
          ClaimStatusHandle(e){
            //   console.log(e);
                let arr = [];
                let newArr = [...e];
                newArr.forEach(item=>{
                  let {ClaimStatus} = item;
                  arr.push({ClaimStatus});
                });
                this.ClaimBtns = arr;

                this.setClaimStatus(arr);
          },
          setClaimStatus(arr){

            this.$refs.pc.ClaimBtns = arr;
            this.$refs.mobile.ClaimBtns = arr;
            // console.log(this.$refs.mobile.ClaimBtns);
          },
    
    },
    
    
    
    
    
    }