<template>

<div class="mining">

  <div class="top">
    Stake LP tokens to earn PGO
  </div>
  <div class="bg">
    Current TVL $ 
    <count-to :start-val="0" :end-val="Number(totalTvl)" :duration="duration" :decimals="2"/>
  </div>
  <div class="datas">
    <div class="tit">
      <div class="left">
          Farms
      </div>
      <div class="right">
        <el-switch
          v-model="value"
          active-color="#0049dc"
          @change="change"
          inactive-color="#738ee4">
        </el-switch>
        <span>Stake Only</span>
        <el-input
          placeholder="search"
          prefix-icon="el-icon-search"
          @input="search"
          v-model="input">
        </el-input>
      </div>
    </div>

    <div class="table">
      
        <el-table
          :boder="false"
          :data="tableData"
          style="width: 100%"
          @row-click="clickTable"
          ref="refTable"
          :border="false"
          class="customer-table"
           @expand-change="expandChange" :expand-row-keys="expands" :row-key='getRowKeys'
          >
          <el-table-column
            prop=""
            label=""
            
            width="20">
          </el-table-column>


          
          <el-table-column type="expand" class-name="fold" width="1">
            <template slot-scope="props">
              <div class="more">
                
                  <span class="href">
                    <a :href="props.row.url" target="_blank">
                      <i class="el-icon-link"></i>
                      <span>View on OKlink</span>
                    </a>
                    
                  </span>
                  <span class="num">
                    <span>{{props.row.name}} LP Staked</span>
                    <span>
                        <count-to :start-val="0" :end-val="Number(props.row.myAmount)" :duration="duration" :decimals="5"/>

                    </span>
                    

                  </span>
                  
                  <span class="btns">
                    <button @click="handle(0,props.row,props.$index)" :disabled="!(Number(props.row.myAmount)>0)">Unstake</button>
                    <button @click="handle(1,props.row,props.$index)">Add</button>

                    


                  </span>
                
              </div>
            </template>
          </el-table-column>

          <el-table-column label="Pool">
            <template slot-scope="scope">
              <div class="imgs">
                  <img :src="scope.row.token0_icon" alt="" srcset="">
                  <img :src="scope.row.token1_icon" alt="" srcset="">
                  <span>{{scope.row.name}}</span>
                  
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label="Total Liquidity"
            class-name="all"
            sortable
            sort-by="totalTvl"
            align="left"
            width="">
            <template slot-scope="scope">
              <div>
                  <span>
                    	
                    $ <count-to :start-val="0" :end-val="Number(scope.row.totalTvl)" :duration="duration" :decimals="2"/>


                  </span>
                  
                  
              </div>
            </template>
          </el-table-column>
          
          <el-table-column
            prop="mulpitlier"
            label="Multiplier"
            sortable
            width="">
          </el-table-column>
          
          <el-table-column label="APY" sortable sort-by="apr">
            <template slot-scope="scope">
              <div class="pop">
                  <span class="color ico">
                    
                    <count-to :start-val="0" :end-val="Number(scope.row.apr)" :duration="duration" :decimals="2"/>%

                  </span>
                  <!-- <el-popover
                    
                    popper-class="tip"
                    placement="top"
                    width="260"
                     
                    trigger="hover">
                    <div class="popTip">
                      <div class="tipTop">
                          <span></span>
                          <span>APR</span>
                          <span>APY</span>
                      </div>
                      <div class="tipTop center">
                          <span>Stake</span>
                          <span>320.98%</span>
                          <span>2,342.89%</span>
                      </div>
                      <div class="tipBottom">
                          每日将收益提取进行手动复投 随着时间的增加，复利收益将会越来越高
                      </div>
                    </div>     
                    <i class="el-icon-info" slot="reference"></i>
                  </el-popover> -->
                  
              </div>
            </template>
          </el-table-column>
          <el-table-column label="Earn">
            <template slot-scope="scope">
              <div class="pop">
                  <span class="color ico">
                    	
                    <count-to :start-val="0" :end-val="Number(scope.row.yield)" :duration="duration" :decimals="scope.row.yield>0?6:0"/> PGO


                  </span>
                  
                  
              </div>
            </template>
          </el-table-column>
          
          <el-table-column label="" width="165">
            <template slot-scope="scope">
              <div class="unfold">

                <el-button type="info" plain :disabled="!(Number(scope.row.yield)>0)"  :loading="ClaimBtns[scope.$index].ClaimStatus" @click.stop="claim(scope.$index,scope.row)">Claim</el-button>
                <i class="el-icon-caret-bottom anim" :class="{ 'arrowTransform': !scope.row.flag, 'arrowTransformReturn': scope.row.flag}" src="arrow.png" alt=""></i>  

                  
                  
              </div>
            </template>
          </el-table-column>




        </el-table>
    </div>
  </div>
  <el-dialog :title="dialog.tit" :visible.sync="dialogVisible" width="400px" top="30vh">
    <div class="popup">
      <div class="balance">
        <span>{{currLp.name}} LP</span>
        <span>Balance {{dialog.balance}}</span>

      </div>
      <div class="ipt">
        <el-input placeholder="" v-model="form._amount" type='number'>
          <template slot="suffix"><span @click="max">Max</span></template>
        </el-input>
      </div>
      <div class="btn">
        <el-button type="info" @click="appr"  :loading="dialog.request"  :disabled="btns.confrim" :class="{ 'able': btns.confrim }"  v-if="!dialog.isApprove&&type==1" >Approve</el-button>
         <el-button type="info" @click="confrim" :disabled="btns.confrim" :loading="dialog.request" :class="{ 'able': btns.confrim }" v-else>Confirm</el-button>

       
      </div>
      <div class="bottom" v-if="dialog.isLink">
        <a href="javascript:;">Obtain {{currLp.name}} LP?</a>
      </div>
    </div>
  </el-dialog>






</div>

</template>

<script>
      
import indexJs from '../../common/miningTableCommon.js'
import CountTo from 'vue-count-to'
export default{
components: {
　　    CountTo
　    },
...indexJs

}

</script>

<style src="./miningTable.css"scoped="scoped">

</style>